import React, { Suspense, lazy } from "react";
import { isTagEmbed, isTagShop, isTagbox } from "../../../../actions/api";
import { IS_THEME } from "../../../../utils";
const Author = lazy(() => import( "../../Elements/Author/Author"));
const Time = lazy(() => import( "../../Elements/Time/Time"));
const Network = lazy(() => import( "../../Elements/Network/Network"));
/*--  Note :  !IS_THEME(59) for Vivid Theme --*/
const AuthorDetails = ({ author, personalization, postTime, network, ThemeRule, itemData, mediaType }) => {
  const username = author.username ? author.username.length > 0 ? `@${author.username}` : `` : '';

  const postAuthor = (personalization.postAuthor && !author.isInstaUser) ? true : false;
  const authorSeprator = (personalization.postAuthor && personalization.postTime && !author.isInstaUser && username != '') ? true : false;
  return <div className="tb_nc_author_wrapper" aria-label="Author details" aria-roledescription="post author details">
    <div className={`tb_nc_author ${isTagEmbed && IS_THEME(19) && mediaType ? 'tb_nc_author_top' : ''}`}>
      {postAuthor ? <Suspense fallback={<></>}><Author itemData={itemData} network={network} author={author} authorClass={`tb_nc_author_profile`} /></Suspense> : ''}
      <div className="tb_nc_author_info">
        {postAuthor ? <div className="tb_nc_authorname">{author.name}</div> : ''}
        <div className="tb_nc_post_info">
          {postAuthor && username != '' ? <div className="tb_nc_username">{username}</div> : ''}
          {authorSeprator ? <div className="tb_nc_seprator"> </div> : ''}
          {personalization.postTime ? <Suspense fallback={<></>}><Time postTime={postTime} timeClass={`tb_nc_time tb-cTBfont-${ThemeRule.font_varient}`} authorColor={ThemeRule.authorColor} /></Suspense> : ''}
        </div>
      </div>
    </div>
    {((isTagEmbed && IS_THEME(19)) && (isTagEmbed && !mediaType)) || IS_THEME(59) || IS_THEME(83) || (IS_THEME(19) && (isTagShop || isTagbox))
      ?
      <div className="tb_nc_social_">
        <Suspense fallback={<></>}><Network networkClass={'tb_nc_social_ico'} network={network} isDefault={ThemeRule.iconType === 1 ? true : false} ThemeRule={ThemeRule} /></Suspense>
      </div>
      : null}
  </div>
}
export default AuthorDetails